  nav {
    position: fixed;
    top: 0;
    padding: 2.5rem;
    width: 100%;
  z-index: 10;
    
  }
  
  nav h2 {
    display: inline;
  }
  
  .menu {
    position: relative;
    margin-top: 6px;
    float: right;
    list-style-type: none;
    transition: top 0.5s cubic-bezier(0.3, 0.1, 0.3, 0.85);
    z-index: 1;

  }
  
  .menu li {
    display: inline;
    margin-right: 15px;
    cursor: pointer;
    color: #fff;
    list-style-type: none;
  }
  
  .menu li::before {
    position: absolute;
    bottom: -6px;
    content: "";
    height: 3px; 
    width: 0;
    background: #333;
    transition: 0.5s;
  }
  
  .menu li:hover::before {
    width: 49px;
  }
  
  .menu li:nth-child(3):hover::before {
    width: 63px;
  }
  
  p {
    margin: 35px 100px;
    font-size: 20px;
  }

  input[type=checkbox] {
    position: absolute;
    top: -9999px;
    left: -9999px;
  }
  
  nav label {
    position: absolute;
    display: none;
    top: 0;
    right: 0;
    height: 70px;
    width: 70px;
    background: transparent;
    cursor: pointer;
    z-index: 2;
  }
  
  nav > label > span {
    display: block;
    margin: 4px auto;
    height: 4px;
    width: 25px;
    border-radius: 1px;
    background: var(--turquoise);
    transition: 0.5s;
  }
  
  nav > label > span:nth-child(1) {
    margin-top: 25px;
  }
  
  input[type=checkbox]:checked ~ .menu {
    top: 0;
  }
  
  input[type=checkbox]:checked ~ label span {
    background: #fff;
  }
  
  input[type=checkbox]:checked ~ label span:nth-child(1) {
    -webkit-transform: translateY(8px) rotate(45deg);
            transform: translateY(8px) rotate(45deg);
  }
  
  input[type=checkbox]:checked ~ label span:nth-child(2) {
    opacity: 0;
  }
  
  input[type=checkbox]:checked ~ label span:nth-child(3) {
    -webkit-transform: translateY(-8px) rotate(-45deg);
            transform: translateY(-8px) rotate(-45deg);
  }
  
  @media (max-width: 320px) {
    .menu {
      position: absolute;
      display: -webkit-flex;
      display: flex;
      -webkit-flex-direction: column;
              flex-direction: column;
      -webkit-align-items: center;
              align-items: center;
      -webkit-justify-content: center;
              justify-content: center;
      margin: 0;
      top: -100vh;
      left: 0;
      height: 100vh;
      width: 100%;
      background-color: rgba(var(--lightpetrolium),0.8);
    }
    
    .menu li {
      margin: 20px;
      font-size: 1.5em;
      color: #fff;
    }
    
    nav label {
      display: block;
    }
    
    p {
      margin: 35px 50px;
    }
  }
  
  


